/**=====================
     brand section css start
==========================**/
#rzp-button1 {
  width: auto;
  height: auto;
  font-size: 16px;
  font-weight: 600;
  border-radius: 0.25rem;
}

.brand-slider {
  margin-bottom: -7px;
}

.zi-1 {
  z-index: 1;
}

.blog-profile {
  display: flex;
  align-items: center;

  .image-profile {
    position: relative;
    width: 55px;
    height: 55px;
    overflow: hidden;
    border-radius: 50%;

    img {
      transition: 0.5s ease;
      width: 100%;
      height: auto;
      object-fit: cover;
    }
  }

  .image-name {
    margin-left: 15px;

    [dir="rtl"] & {
      margin-left: unset;
      margin-right: 15px;
    }

    .text-weight {
      h6 {
        font-weight: 300;
      }
    }

    h3 {
      position: relative;
      font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 500;
      margin-bottom: 4px;
    }

    h6 {
      color: $light-grayish-blue;
      font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (1920 - 320)));
    }
  }
}

.search-section {
  .search-bar {
    .input-group {
      width: 50%;
      margin: 26px auto 110px;

    }

    button {
      padding: 15px 35px;
      background-color: var(--theme-color);
      color: $white;
      border: 1px solid var(--theme-colors);

      i {
        color: $white;
      }

      &.search-button {
        padding: 15px;
      }
    }

    input {
      &:focus {
        border-color: $border-color;
      }
    }
  }
}

/**=====================
       Social media css start
  ==========================**/
.social-media {
  display: flex;

  &.media-center {
    justify-content: center;
  }

  a {
    +a {
      margin-left: calc(8px + (15 - 8) * ((100vw - 320px) / (1920 - 320)));

      [dir="rtl"] & {
        margin-left: unset;
        margin-right: calc(8px + (15 - 8) * ((100vw - 320px) / (1920 - 320)));
      }
    }
  }

  .social-icon-box {
    width: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
    height: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
    background-color: $black1;
    @include flex_common;
    border-radius: 4px;

    // @include mq-max(sm) {}

    +.social-meida {
      margin-left: calc(6px + (15 - 6) * ((100vw - 320px) / (1920 - 320)));
    }

    &.social-color {
      background-color: $light-blue;

      @include mq-max(2xs) {
        background-color: var(--theme-color);
      }

      i {
        color: $black1;

        @include mq-max(sm) {
          font-size: 12px;
        }

        @include mq-max(2xs) {
          color: $white;
        }
      }
    }

    i {
      color: $white;
    }
  }
}

/**=====================
     Load more css start
  ==========================**/
.load-more {
  text-align: center;
  margin: 40px auto 0;
  width: 280px;

  a {
    font-size: 18px;
    text-transform: uppercase;
    display: block;
    padding: 10px 0;
    border-top: 1px solid $gray-light;
    border-bottom: 1px solid $gray-light;
    width: 100%;
    color: var(--theme-color);
  }
}

.product-load-more {
  .col-grid-box {
    display: none;
  }
}

/**=====================
       Log In page Css Start
  ==========================**/
.login-section {
  min-height: 100vh;
  position: relative;
  background: $demo1-bg;
  @include flex_common;
  padding: calc(30px + (50 - 10) * ((100vw - 320px) / (1920 - 320)));

  .materialContainer {
    width: 100%;
    max-width: 460px;
    position: relative;
    top: 0;
    left: 0;
    transform: translate(0);

    .box {
      position: relative;
      top: 0;
      opacity: 1;
      float: left;
      padding: calc(30px + (60 - 30) * ((100vw - 320px) / (1920 - 320))) calc(10px + (30 - 10) * ((100vw - 320px) / (1920 - 320)));
      width: 100%;
      background: $white;
      border-radius: 10px;
      transform: scale(1);
      z-index: 5;

      &.back {
        transform: scale(0.95);
        top: -20px;
        opacity: 0.8;
        z-index: -1;
      }

      &:before {
        @include pos;
        width: 100%;
        height: 30px;
        border-radius: 10px;
        top: -10px;
        background-color: rgba($white, $alpha: 0.6);
        left: 0;
        transform: scale(0.95);
      }
    }

    .login-title {
      width: 100%;
      line-height: 46px;
      font-size: 34px;
      font-weight: 700;
      letter-spacing: calc(0px + (2 - 0) * ((100vw - 320px) / (1920 - 320)));
      color: var(--theme-color);
      position: relative;

      &:before {
        @include pos;
        width: 0px;
        height: 100%;
        top: 0;
        left: calc(-10px + (-10 - 10) * ((100vw - 320px) / (1920 - 320)));
        background: var(--theme-color);

        [dir="rtl"] & {
          left: unset;
          right: calc(-10px + (-10 - 10) * ((100vw - 320px) / (1920 - 320)));
        }
      }

      h2 {
        font-size: calc(27px + (28 - 27) * ((100vw - 320px) / (1920 - 320)));
        margin-left: calc(10px + (0 - 10) * ((100vw - 320px) / (1920 - 320)));
        margin-top: -2px;
      }
    }

    .social-media {
      padding: 15px calc(10px + (25 - 10) * ((100vw - 320px) / (1920 - 320)));
      border-radius: 4px;
      box-shadow: 0 0 10px rgba($gray, $alpha: 0.6);
      @include flex_common;

      h6 {
        margin-left: 20px;
        margin-bottom: -2px;
        color: $white;

        [dir="rtl"] & {
          margin-left: 0;
          margin-right: 20px;
        }
      }

      img {
        width: calc(8% + (18 - 8) * ((100vw - 320px) / (1920 - 320)));
      }

      &.fb-media {
        background-color: $dark-blue;

        h6 {
          color: $white;
          font-size: 18px;
        }
      }

      &.google-media {
        color: $black;

        h6 {
          font-size: 18px;
          color: $black;
        }
      }
    }

    p {
      width: 100%;
      float: left;
      text-align: center;
      color: rgba($black1, $alpha: 0.4);
      font-size: calc(13px + (15 - 13) * ((100vw - 320px) / (1920 - 320)));
      margin-top: calc(17px + (23 - 17) * ((100vw - 320px) / (1920 - 320)));
      margin-bottom: 0;

      &.pass-or {
        margin-top: calc(4px + (12 - 4) * ((100vw - 320px) / (1920 - 320)));
      }
    }

    .input {
      transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);

      label,
      input,
      .spin {
        transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
      }
    }

    .button {
      transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);

      button .button.login button i.fa {
        transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
      }
    }

    .material-button .shape {

      &:before,
      &:after {
        transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
      }
    }

    .button.login button {
      transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .material-button,
    .alt-2,
    .material-button .shape,
    .alt-2 .shape,
    .box {
      transition: 400ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    .input {
      width: 100%;
      float: left;

      label,
      input,
      .spin {
        width: 100%;
        float: left;
      }
    }

    .button {
      width: 100%;
      float: left;

      button {
        width: 100%;
        float: left;
      }
    }

    .input {
      margin-top: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
      height: 70px;
    }

    .input {
      position: relative;

      input {
        position: relative;
      }
    }

    .button {
      position: relative;

      button {
        position: relative;
      }
    }

    .input {
      input {
        height: 60px;
        top: 10px;
        border: none;
        background: transparent;
        font-family: "Roboto", $font_1;
        font-size: 18px;
        color: $black1;
        font-weight: 300;
      }

      &.form-floating {
        height: auto;
      }

      select {
        height: 76px;
        top: 10px;
        border: none;
        background: transparent;
        font-family: "Roboto", $font_1;
        font-size: 16px;
        color: #777777;
        font-weight: 300;
        padding: 0;
      }

      label {
        font-family: "Roboto", $font_1;
        font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
        color: rgba($black1, 0.8);
        font-weight: 300;

        &.email-label {
          font-size: calc(15px + (24 - 15) * ((100vw - 320px) / (1920 - 320)));
        }
      }
    }

    .button {
      button {
        font-family: "Roboto", $font_1;
        font-size: 24px;
        color: rgba($black1, 0.8);
        font-weight: 300;
      }
    }

    .input {

      &:before,
      .spin {
        width: 100%;
        height: 1px;
        position: absolute;
        bottom: 0;
        left: 0;

        [dir="rtl"] & {
          left: unset;
          right: 0;
        }
      }

      &:before {
        content: "";
        background: rgba($black1, $alpha: 0.15);
        z-index: 3;
      }

      .spin {
        background: var(--theme-color);
        z-index: 4;
        width: 0;
      }
    }

    .overbox .input {
      .spin {
        background: $white;
      }

      &:before {
        background: rgba($white, 0.5);
      }
    }

    .input {
      label {
        position: absolute;
        top: 10px;
        left: 0;
        z-index: 2;
        cursor: pointer;
        line-height: 60px;
        color: $shade-gray;
      }
    }

    .button {
      margin-top: 20px;
      margin: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320))) 0;
      overflow: hidden;
      z-index: 2;

      &.button-1 {
        margin-bottom: 0;
      }

      &.login {
        button {
          width: 100%;
          left: 0%;
          background-color: var(--theme-color);
          border: 1px solid var(--theme-color);
          font-weight: 900;
          font-size: 18px;
          color: $white;
          padding: calc(13px + (18 - 13) * ((100vw - 320px) / (1920 - 320)));
          border-radius: 4px;

          span {
            display: block;
            margin-top: -3px;
          }
        }
      }

      button {
        width: 100%;
        left: 0%;
        background-color: transparent;
        border: 1px solid rgba($black1, 0.1);
        font-weight: 900;
        font-size: 18px;
        color: rgba($black1, 0.2);
        padding: calc(13px + (18 - 13) * ((100vw - 320px) / (1920 - 320)));
      }

      button {
        background-color: $white;
        color: var(--theme-color);
        border: none;
      }

      &.login button {
        &.active {
          border: 1px solid transparent;
          color: $white !important;
          background-color: var(--theme-color);

          span {
            opacity: 0;
            transform: scale(0);
            display: none;
          }

          i.fa {
            opacity: 1;
            transform: scale(1) rotate(-0deg);
            display: block;
          }
        }

        i.fa {
          @include pseudowh;
          position: relative;
          top: 0;
          left: 0;
          font-size: 18px;
          transform: scale(0) rotate(-45deg);
          display: none;
        }
      }

      button {
        cursor: pointer;
        position: relative;
        z-index: 2;
      }
    }

    .pass-forgot {
      width: 100%;
      float: right;
      text-align: right;
      color: rgba($black1, $alpha: 0.4);
      font-size: calc(13px + (15 - 13) * ((100vw - 320px) / (1920 - 320)));
      margin: 10px auto 0;

      &:hover {
        color: var(--theme-color);
      }
    }

    .sign-category {
      width: 100%;
      text-align: center;
      color: rgba($black1, $alpha: 0.4);
      font-size: calc(13px + (18 - 13) * ((100vw - 320px) / (1920 - 320)));
      margin: 10px auto;
      position: relative;

      span {
        background-color: $white;
        z-index: 1;
        padding: 0 12px;
      }

      &::after {
        @include pos;
        @include center(vertical);
        left: 0;
        width: 100%;
        height: 1px;
        color: rgba($black1, $alpha: 0.2);
        z-index: -1;
      }
    }

    .overbox {
      @include pseudowh;
      position: absolute;
      top: 0;
      left: 0;
      overflow: inherit;
      border-radius: 10px;
      padding: 60px 50px 40px 50px;

      .login-title,
      .button,
      .input {
        z-index: 111;
        position: relative;
        color: $white !important;
        display: none;
      }

      .login-title {
        width: 80%;
      }

      .input {
        margin-top: 20px;

        input,
        label {
          color: $white;
        }
      }

      .material-button,
      .alt-2 {
        display: block;

        .shape {
          display: block;
        }
      }
    }

    .material-button,
    .alt-2 {
      width: 140px;
      height: 140px;
      border-radius: 50%;
      background: var(--theme-color);
      position: absolute;
      top: 40px;
      right: -70px;
      cursor: pointer;
      z-index: 100;
      transform: translate(0%, 0%);
    }

    .material-button,
    .alt-2 {
      .shape {
        position: absolute;
        top: 0;
        right: 0;
        @include pseudowh;
      }
    }

    .material-button .shape:before,
    .alt-2 .shape:before,
    .material-button .shape:after,
    .alt-2 .shape:after {
      @include pos;
      background: $white;
      @include center(both);
    }

    .material-button .shape:before,
    .alt-2 .shape:before {
      width: 25px;
      height: 4px;
    }

    .material-button .shape:after,
    .alt-2 .shape:after {
      height: 25px;
      width: 4px;
    }

    .material-button.active,
    .alt-2.active {
      top: 50%;
      right: 50%;
      transform: translate(50%, -50%) rotate(0deg);
    }
  }
}

/**=====================
       404 Page Css Start
  ==========================**/
.page-not-found {
  .page-image {
    text-align: center;

    img {
      width: 60%;
    }
  }

  .page-container {
    @include flex_common;
    text-align: center;
    padding: 0 calc(0px + (50 - 0) * ((100vw - 320px) / (1920 - 320)));
    margin-top: calc(10px + (20 - 10) * ((100vw - 320px) / (1920 - 320)));

    h2 {
      margin-bottom: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320)));
      color: $black1;
      font-size: calc(25px + (35 - 25) * ((100vw - 320px) / (1920 - 320)));
      margin-top: -3px;
    }

    p {
      font-size: 16px;
      line-height: 1.6;
      color: $font-light;
    }
  }
}

/**=====================
       Contact Page Css Start
  ==========================**/
.contact-section {
  .materialContainer {
    width: 100%;
    position: relative;
    top: 0;
    left: 0;
    transform: translate(0);

    .material-details {
      h2 {
        margin-top: -5px;
      }

      h4 {
        margin-top: 10px;
        font-weight: 400;
        color: $bermuda-grey;
        line-height: 1.5;
        font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
      }
    }

    .box {
      position: relative;
      top: 0;
      opacity: 1;
      float: left;
      width: 100%;
      background: $white;
      border-radius: 10px;
      transform: scale(1);
      z-index: 5;
    }

    .input {
      transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);

      label,
      input,
      textarea,
      .spin {
        transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
      }
    }

    .input {
      margin-top: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
      height: 70px;
    }

    .input {
      position: relative;

      input {
        position: relative;
      }

      textarea {
        position: relative;
        width: 100%;
      }
    }

    .input {

      input,
      textarea {
        height: 60px;
        top: 10px;
        border: none;
        background: transparent;
        font-family: "Roboto", $font_1;
        font-size: 18px;
        color: rgba($black1, 0.8);
        font-weight: 300;
      }

      label {
        font-family: "Roboto", $font_1;
        font-size: calc(13px + (18 - 13) * ((100vw - 320px) / (1920 - 320)));
        color: rgba($black1, 0.8);
        font-weight: 300;

        &.email-label {
          font-size: calc(15px + (24 - 15) * ((100vw - 320px) / (1920 - 320)));
        }
      }
    }

    .input {

      &:before,
      .spin {
        width: 100%;
        height: 1px;
        position: absolute;
        bottom: 0;
        left: 0;
      }

      &:before {
        content: "";
        background: rgba($black1, 0.1);
        z-index: 3;
      }

      .spin {
        background: var(--theme-color);
        z-index: 4;
        width: 0;
      }
    }

    .input label {
      position: absolute;
      top: 10px;
      left: 0;
      z-index: 2;
      cursor: pointer;
      line-height: 60px;
      color: $bermuda-grey;
    }
  }

  .location-map {
    iframe {
      width: 100%;
      height: calc(200px + (340 - 200) * ((100vw - 320px) / (1920 - 320)));
      margin-bottom: -6px;
    }
  }

  .contact-details {
    background-color: $light-blue;
    height: 100%;
    padding: calc(21px + (30 - 21) * ((100vw - 320px) / (1920 - 320)));
    display: flex;
    align-items: center;

    h2 {
      font-weight: 500;
      margin-top: -3px;
      margin-bottom: 10px;
      font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
    }

    h5 {
      font-weight: 400;
      margin-bottom: 30px;
    }

    .contact-box {
      margin-top: 25px;
      display: flex;
      align-items: flex-start;

      .contact-title {
        width: calc(80% - 80px);
        margin-left: calc(10px + (20 - 10) * ((100vw - 320px) / (1920 - 320)));

        [dir="rtl"] & {
          margin-left: unset;
          margin-right: calc(10px + (20 - 10) * ((100vw - 320px) / (1920 - 320)));
        }

        @include mq-max(lg) {
          width: calc(100% - 80px);
        }

        h4 {
          margin-bottom: 10px;
          font-weight: 500;
        }

        p {
          margin-bottom: 0;
          letter-spacing: 0.6px;
          line-height: 26px;

          +p {
            margin-top: 0;
            margin-bottom: -2px;
          }
        }
      }

      .contact-icon {
        width: calc(35px + (45 - 35) * ((100vw - 320px) / (1920 - 320)));
        height: calc(35px + (45 - 35) * ((100vw - 320px) / (1920 - 320)));
        border-radius: 100%;
        @include flex_common;
        position: relative;
        overflow: hidden;

        .feather {
          width: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
          stroke: var(--theme-color);
        }

        &:after {
          @include pos;
          @include pseudowh;
          left: 0;
          top: 0;
          background-color: var(--theme-color);
          opacity: 0.12;
          border-radius: 100%;
        }
      }
    }
  }
}

/**=====================
       FAQ Page Css Start
  ==========================**/
.search-title {
  margin-bottom: calc(30px + (50 - 30) * ((100vw - 320px) / (1920 - 320)));
  text-align: center;

  h1 {
    font-size: calc(30px + (50 - 30) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 500;
    display: block;
    margin-top: -10px;
    text-transform: none;
  }
}

.faq-section {
  margin-top: -160px;

  .faq-contain {
    text-align: center;
    padding: calc(22px + (40 - 22) * ((100vw - 320px) / (1920 - 320)));
    border-radius: 10px;
    height: 100%;
    transition: 0.5s ease;
    z-index: 1;
    background-color: $white;
    box-shadow: 0 8px 40px 0 rgba($black1, 0.1);

    .faq-image {
      width: 60px;
      height: 60px;
      @include flex_common;
      margin: 0 auto;

      img {
        object-fit: contain;
      }
    }

    h2 {
      margin-top: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
      font-size: calc(20px + (22 - 20) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 500;
      color: $black1;
    }

    h5 {
      color: $font-light;
      font-weight: 300;
      margin-top: 10px;
      line-height: 1.6;
      margin-bottom: -2px;
      margin: 0 auto -2px;
      letter-spacing: 0.03em;
    }
  }
}

.faq-details {
  .faq-link-box {
    display: flex;
    align-items: flex-start;
    position: sticky;
    top: 30px;

    ul {
      li {
        display: block;

        &:first-child {
          a {
            padding-top: 0;
          }
        }
      }
    }

    a {
      display: flex;
      align-items: flex-start;
      position: relative;
      padding-bottom: 12px;
      padding-top: 12px;
      color: $black1;

      &:after {
        @include pos;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background-color: rgba(120, 144, 174, 0.23);

        @include mq-max(sm) {
          opacity: 0;
        }
      }

      h4 {
        font-weight: 200;
        font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
      }

      h5 {
        font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 200;
        margin-left: 10px;

        [dir="rtl"] & {
          margin-left: 0;
          margin-right: 10px;
        }
      }

