.custome-nav-tabs {
  padding: 20px;
  color: black;
  border: none;
  border-radius: 5px;
  position: sticky;
  top: 10px;
  background: $light-blue;

  [dir="rtl"] & {
    padding: 20px;
  }

  @include mq-max(lg) {
    position: fixed;
    z-index: 9;
    top: 0;
    left: -300px;
    width: 300px;
    height: 100%;
    z-index: 1;
    border-radius: 0;
    transition: all 0.3s ease-in-out;

    &.show {
      left: 0;
    }
  }

  .nav-item {
    .nav-link {
      width: 100%;
      text-align: left;
      border-radius: 5px;
      background: transparent;
      border: 1px solid transparent;
      position: relative;
      font-size: 16px;
      transition: all 0.3s ease-in-out;
      color: $black1 !important;

      [dir="rtl"] & {
        text-align: right;
      }

      i {
        position: absolute;
        left: 0;
        @include center(vertical);
        line-height: 1;
        transition: all 0.5s ease-in-out;

        [dir="rtl"] & {
          top: 3px;
          left: unset;
          right: 0;
          transform: rotate(180deg) translateY(-50%);
        }
      }

      &:before {
        content: "\f101";
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        position: absolute;
        left: 0;
        @include center(vertical);
        transition: all 0.3s ease-in-out;
        opacity: 0;

        [dir="rtl"] & {
          left: unset;
          right: 0;
          top: 0;
          transform: rotate(180deg) translateY(-50%);
        }
      }

      &:hover {
        padding-left: 20px;
        color: var(--theme-color) !important;

        [dir="rtl"] & {
          padding-left: 0;
          padding-right: 20px;
        }

        &:before {
          opacity: 1;
          left: 0px;

          [dir="rtl"] & {
            left: unset;
            right: 0;
          }
        }
      }

      &.active {
        padding-left: 20px;
        color: var(--theme-color) !important;
        font-weight: 600;

        [dir="rtl"] & {
          padding-left: 0;
          padding-right: 20px;
        }

        &:before {
          opacity: 1;
          line-height: 1;
          left: 0px;

          [dir="rtl"] & {
            left: unset;
            right: 0;
            top: 0;
            transform: rotate(180deg) translateY(-50%);
          }
        }
      }
    }
  }
}

