        position: relative;
        top: 10px;
        float: left;
        left: -2px !important;
        line-height: 1;
        transform: translate(-50%, -50%) !important;

        [dir="rtl"] & {
          float: right;
          right: -32px !important;
          left: unset !important;
        }
      }
    }

    &.progtrckr-todo {
      color: $silver;
      border-top: 4px solid $silver;
      width: 100% !important;

      @include mq-max(sm) {
        position: relative;
        text-align: left;
