        border-top: unset;
        border-left: 4px solid $silver;

        [dir="rtl"] & {
          border-left: none;
          border-right: 4px solid $silver;
          margin-left: 0;
          margin-right: 30px;
          align-items: flex-end;
          padding-left: 0;
          padding-right: 30px;
          text-align: right;
        }
      }

      &:before {
        content: "\f111";
        font-family: "Font Awesome 5 Free";
        color: $silver;
        background-color: $white;
        font-size: 2.2em;

        @include mq-max(sm) {
          position: absolute;
          top: 10px;
          left: -17px;
          line-height: 1;
          transform: translateY(-50%);

          [dir="rtl"] & {
            left: unset;
            right: -17px;
          }
        }
      }

      h6 {
        font-size: 13px;
        margin-top: 8px;
      }
    }

    &.progtrckr-done {
      color: $black1;
      border-top: 4px solid var(--theme-color);
      width: 100% !important;

      @include mq-max(sm) {
        position: relative;
