.btn-solid-dark {
  color: #2b2b2b;
  background-color: unset;
  border: 1px solid #efefef;
}
.btn-solid-dark::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: #efefef;
  transition: all 0.5s;
}
.btn-solid-dark:hover {
  color: #efefef;
}
.btn-solid-dark:hover::before {
  opacity: 0;
  transform: scale(0.5, 0.5);
}

.btn-solid-light {
  color: #2b2b2b;
  background-color: unset;
  border: 1px solid #fefefe;
  color: #2b2b2b;
}
.btn-solid-light::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: #fefefe;
  transition: all 0.5s;
}
.btn-solid-light:hover {
  color: #efefef;
}
.btn-solid-light:hover::before {
  opacity: 0;
  transform: scale(0.5, 0.5);
}
.btn-solid-light:hover, .btn-solid-light:focus, .btn-solid-light:active, .btn-solid-light.active {
  color: #efefef;
}

.btn-solid-default {
  color: #2b2b2b;
  background-color: unset;
  border: 1px solid var(--theme-color);
}
.btn-solid-default::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: var(--theme-color);
  transition: all 0.5s;
}
.btn-solid-default:hover {
  color: #efefef;
}
.btn-solid-default:hover::before {
  opacity: 0;
