==========================**/
body {
  font-family: Rubik, sans-serif;
  position: relative;
  font-size: 14px;
  color: #efefef;
  margin: 0;
  background-color: #2b2b2b;
}
body .bg-overlay {
  height: 100vh;
  width: 100vw;
  background-color: rgba(239, 239, 239, 0.2);
  position: fixed;
  z-index: 3;
  top: 0;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
}
body .bg-overlay.show {
  visibility: visible;
  opacity: 1;
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}

li {
  display: inline-block;
  font-size: 14px;
}

p {
  font-size: 14px;
  line-height: 18px;
}

a {
  color: var(--theme-color);
  transition: 0.5s ease;
  text-decoration: none;
}
a:hover {
  text-decoration: none;
  transition: 0.5s ease;
}
a:focus {
  outline: none;
}

button:focus {
  outline: none;
}

.btn-close:focus {
  box-shadow: none;
}

:focus {
  outline: none;
}

.form-control {
  background-color: #2b2b2b;
}
.form-control:focus {
  box-shadow: none;
}

h1 {
  font-size: calc(40px + 30 * (100vw - 320px) / 1600);
