/**=====================
     dropdown css start
==========================**/
.onhover-div {
  top: 70px;
  opacity: 0;
  visibility: hidden;
  position: absolute;
  z-index: 8;
  background-color: #2b2b2b;
  transition: all linear 0.3s;
  min-width: 280px;
  box-shadow: 3.346px 3.716px 22.5px rgba(239, 239, 239, 0.07);
}
.onhover-div li {
  font-size: 14px;
}
.onhover-div li a svg {
  color: #efefef;
}
.onhover-div::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 7px solid #2b2b2b;
  top: -7px;
  right: 10px;
  z-index: 2;
}
[dir=rtl] .onhover-div::before {
  right: unset;
  left: 10px;
}

.onhover-dropdown {
  position: relative;
  cursor: pointer;
}
.onhover-dropdown:hover .onhover-div {
  top: 50px;
  opacity: 1;
  transform: translateY(0px);
  visibility: visible;
}
@media (max-width: 767px) {
  .onhover-dropdown:hover .onhover-div {
    width: 320px;
  }
}
.onhover-dropdown.cart-dropdown button {
  display: flex;
  align-items: center;
  justify-content: center;
}
.onhover-dropdown.cart-dropdown button .feather {
  width: 23px !important;
  margin-right: 10px;
  height: 22px;
}
[dir=rtl] .onhover-dropdown.cart-dropdown button .feather {
  margin-right: unset;
  margin-left: 10px;
}
@media (max-width: 360px) {
  .onhover-dropdown.cart-dropdown button .feather {
    margin-right: 0;
  }
  [dir=rtl] .onhover-dropdown.cart-dropdown button .feather {
    margin-right: unset;
    margin-left: 0;
  }
}
.onhover-dropdown.cart-dropdown button span {
  font-size: calc(14px + 2 * (100vw - 320px) / 1600);
}

